import { getDefaultOptions, request } from 'api/helpers';

const resource = 'conference-alessio-bundles';

export const apiConferenceAlessioBundleGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiConferenceAlessioBundlePost = async (serviceUrl, conferenceAlessioBundle) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: conferenceAlessioBundle ? JSON.stringify(conferenceAlessioBundle) : null,
  };
  return request(url, options);
};

